import Vue from 'vue'
import Vuex from 'vuex'

import {CHANGEINFO,INCREMENT} from './events'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    count: 0,
    info:{}
  },
  getters:{
    fcount:state=>{
      return state.count*2;
    },
    mincount:state=>{
      return state.count;
    },
    maxcount:(state,getters)=>{
      return getters.mincount*3;
    }
  },
  mutations: {
    [INCREMENT] (state) {
      state.count++
    },
    [CHANGEINFO] (state,data){
      if (state.info.msg)
        state.info.msg = data.count+'==>:'+data.msg;
      else
        state.info = {msg:data.count+'==>:'+data.msg,...state.info};
    }
  }
})



