<template>
  <div class="tem-content">
    {{msg}}| <br/>
    count:{{count}}| <br/>
    {{currentCount}}----{{aa}}|<br/>
    fcount:{{fcount}}  <br/>
    maxcount:{{maxcount}} <br/>
    mincount:{{mincount}} <br/>
    <button @click="changeInfo3">++</button>
    <div style="height: 300px;width: 600px;background: green;color: #fff;">
      <input v-model="msg" />
      <button @click="changeInfo" >
        Change1
      </button><br/>
      {{info.msg}}<br/>
      <input v-model="msg2" />
      <button @click="changeInfo2" >
        Change2
      </button><br/>
      {{info.msg}}
    </div>
  </div>
</template>

<script>
  import {mapState,mapGetters,mapMutations} from 'vuex'
  import * as e from '../../store/events'

  var bv = {
    aa:'jjjjj',
    bb:'2222'
  };
export default {
  name: 'home',
  data () {
    return {
      msg: 'Welcome to Home',
      msg2: 'Welcome to Home',
      ...bv
    }
  },
  methods:{
      add(){
        this.$store.commit(e.INCREMENT);
      },
      changeInfo(){
          this.$store.commit(e.CHANGEINFO,{msg:this.msg,count:this.count})
      },
      changeInfo2(){
          this.$store.commit({type:e.CHANGEINFO,
            msg:this.msg2,count:this.count});
      },
      ...mapMutations({
        changeInfo3: e.INCREMENT // 映射 this.add() 为 this.$store.commit('increment')
      })
  },
  computed:{
    currentCount(){
      return this.count+1;
    },
    fcount(){
      return this.$store.getters.fcount;
    },
    maxcount(){
      return this.$store.getters.maxcount;
    },
    // 使用对象展开运算符将此对象混入到外部对象中
    /*...mapState({
      count:'count'
    },),*/
    ...mapState([
      'count',
      'info'
    ],),
    ...mapGetters([
      'mincount',
      // ...
    ])
  },
  mounted(){
      console.log('home-mounted');
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .tem-content
   {
     height: 100%;
     background: #edf2f5;
   }

</style>



