<template>
  <div class="tem-content">
    <div class="leftDiv" >
      <div class="leftTop">
        <span style="margin-left: 32px;">常规设置</span>
      </div>
      <ul id="nav-List" >
        <li v-for="(it,index) in navList" @click="navClick(index)" :class="{active:navIndex==index}">
          {{it.name}}
        </li>
      </ul>
    </div>
    <div class="rightDiv">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      msg: 'Welcome to Set',
      navList: [{name:'设备管理',url:'Devices'},{name:'教室管理',url:'ClassRooms'},{name:'卡管理',url:'Cards'},
        {name:'管理员设置',url:'Managers'},{name:'机构管理',url:'Organizations'},{name:'用户管理',url:'Users'},
        {name:'班级管理',url:'Classes'},{name:'设备配置更新',url:'DeviceConfig'},{name:'巡考计划',url:'PatrolPlan'},
        {name:'督课模板',url:'SuperviseModel'}],
      navIndex: 0
    }
  },
  methods:
  {
    navClick(index){
      this.navIndex = index;
      this.$router.push('/Set/'+this.navList[index].url);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .tem-content
  {
    height: 100%;
    background: #fff;
  }
  .leftDiv
  {
    width: 11.5%;
    height: 100%;
    float: left;
    background: #36393c;
    color: #b2b2b2;
    font-size: 16px;
  }
  .rightDiv
  {
    float: right;
    width: 88.5%;
    height: 100%;
  }
  .leftTop
  {
    height: 50px;
    line-height: 50px;
  }
  ul#nav-List{
    padding-left: 20px;
  }
  ul,li{
    list-style: none;
    margin: 0px;
    overflow: hidden;
  }

  ul#nav-List>li{
    padding: 15px 0 15px 10px;
    cursor: pointer;
  }

  ul#nav-List>li.active{
    color: #FFFFFF;
    background: #2dabff;
  }
</style>



