<template>
  <div class="content" >
    <div class="titleStyle-home">
      <div class="titleLeft">
        <img id="logo" src="../assets/picture/logo.png" >
        <span class="systemTitle" >
                中庆设备集控中心
        </span>
        <span class="vnum" >
                v1.0.1
        </span>
      </div>
      <div class="pageTitle">
        2016-2017第一学期 第8周
        <span id="time" >2017-3-23 10:23</span>
      </div>
      <div class="titleRight">
        <img class="seticon" @click="setting" src="../assets/picture/setting.png" />
        <img class="seticon" src="../assets/picture/note.png" />
        <img class="seticon" src="../assets/picture/message.png" />
        <img id="headerimg" src="../assets/picture/cover.png" />
        <span id="userName" >
                周晓明
        </span>
        <img style="margin-right: 91px;" src="../assets/picture/pull_down.png" />
      </div>
    </div>
    <div class="left-manifest" :style="{width: widthVal}" >
      <div class="left-top">
        <img :src="collapsed?unfold:fold" @click="collapsed=!collapsed" />
      </div>
      <ul>
          <li :key="it" v-for="(it,index)  in manifest" :class="{index_active:index==selectedIndex}" @click="setManifest(index)" >
            <img :src="index==selectedIndex?it.onicon:it.icon" />
            <transition name="listLi">
              <span v-if="!collapsed">
                {{it.title}}
              </span>
            </transition>
          </li>
      </ul>
    </div>
    <div class="page-content" id="page-content" :style="{width:contentWidthVal,marginLeft:widthVal}" >
        <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import unfold from '../assets/icon/unfold.png';
  import fold from '../assets/icon/fold.png';

  import home_on from '../assets/icon/home_on.png';
  import home from '../assets/icon/home.png';
  import control_on from '../assets/icon/control_on.png';
  import control from '../assets/icon/control.png';
  import exam_on from '../assets/icon/exam_on.png';
  import exam from '../assets/icon/exam.png';
  import search_on from '../assets/icon/search_on.png';
  import search from '../assets/icon/search.png';
  import order_on from '../assets/icon/order_on.png';
  import order from '../assets/icon/order.png';
  import video_on from '../assets/icon/video_on.png';
  import video from '../assets/icon/video.png';

  var vuecom;
  window.onresize=function(){
    vuecom.screenWidth = document.documentElement.clientWidth;
  }

export default {
  name: 'index',
  data () {
    return {
      collapsed: false,
      unfold:unfold,
      fold:fold,
      selectedIndex: 0,
      screenWidth: document.documentElement.clientWidth,
      leftHeight: "100%",
      manifest:[
        {title:'首页',onicon:home_on,icon:home,url:'Home'},
        {title:'集控',onicon:control_on,icon:control,url:'CentralizedManager'},
        {title:'巡考',onicon:exam_on,icon:exam,url:'PatrolExam'},
        {title:'巡视',onicon:search_on,icon:search,url:'Patrol'},
        {title:'预约',onicon:order_on,icon:order,url:'Subscribe'},
        {title:'录像',onicon:video_on,icon:video,url:'Video'}
      ]
    }
  } ,
  methods:  {
    setManifest(index)
    {
        this.selectedIndex = index;
        this.$router.push('/'+this.manifest[index].url);
    },
    setting(){
      this.selectedIndex = -1;
      this.$router.push('/Set/Devices');
    }
  },
  computed: {
      widthVal(){
          return this.collapsed?'92px':'13%';
      },
      contentWidthVal(){
          if (this.collapsed)
              return this.screenWidth-92 +'px';
          else
              return this.screenWidth*0.87 +'px';
      },
      contentHeightVal(){
          var content = document.getElementsByClassName('page-content');
          if (content.length>0)
              return content[0].style.height+'px';
          else
              return '100%';
      }
  },
  created()
  {
      vuecom = this;
  },
  updated()
  {
    var content = document.getElementById('page-content');
    this.screenWidth = document.documentElement.clientWidth; // 更新右侧宽度
    /*var res = 0;
    if (content)
      res = content.offsetHeight;
    this.leftHeight = (res>document.documentElement.clientHeight)?(res+'px'):'100%';*/
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .content{
    background: #edf2f5;
    height: 100%;
  }
  .listLi-enter-active, .listLi-leave-active {
    transition: opacity 0.5s
  }
  .listLi-enter, .listLi-leave-active {
    opacity: 0
  }
  .left-manifest{
    transition: all 0.5s;
    float: left;
    height: calc(100% - 70px);
    background: #2e6599;
    position: fixed;
    box-shadow: 20px 0px 20px rgba(0,0,0,0.1)
  }
  .left-manifest ul{
    padding: 0px;
    margin: 0px;
    list-style: none;
  }

  .left-manifest li{
    height: 60px;
    line-height: 60px;
    color: #AAE7F2;
    font-size: 16px;
    text-align: left;
    cursor: pointer;
  }

  .left-top{
    overflow: hidden;
  }

  .left-top img
  {
    margin: 20px 20px auto;
    margin: 20px 20px 20px auto;
    float: right;
    cursor: pointer;
  }

  .page-content
  {
    transition: all 0.5s;
    float: left;
    height: calc(100% - 70px);
  }

  .index_active
  {
    background: #fff;
    color: #455258;
    font-size: 16px;
  }

  ul img
  {
    margin-left: 30px;
    margin-right: 20px;
    position: relative;
    top: 7px;
    max-width: 28px;
  }
  .titleStyle-home{
    position: relative;
    background-color: #ffffff;
    height: 70px;
    margin: 0;
    line-height: 70px;
    box-shadow: 0px 10px 20px rgba(0,0,0,.05);
  }
  .titleLeft{
    float: left;
    height: 70px;
    line-height: 70px;
    width: 33.33%;
  }

  .titleLeft #logo
  {
    position: relative;
    top: 11.3px;
    margin: auto 20px auto 26px;
  }

  .titleLeft .systemTitle
  {
    font-size: 24px;
    color: #323232;
  }

  .titleLeft .vnum
  {
    font-size: 18px;
    color: #b2b2b2;
    margin-left: 10px;
  }

  .titleRight
  {
    float: right;
    height: 70px;
    line-height: 70px;
  }

  .titleRight #headerimg
  {
    margin-left: 28px;
    max-width: 30px;
    border-radius:5px
  }

  .titleRight .seticon
  {
    margin-left: 20px;
  }

  .titleRight > img
  {
    position: relative;
    top: 7px;
    cursor: pointer;
  }

  .titleRight #userName
  {
    color: #2dabff;
    font-size: 18px;
    margin: 0px 10px;
  }

  .pageTitle
  {
    text-align: center;
    float: left;
    height: 70px;
    margin: 0;
    line-height: 70px;
    font-size: 18px;
    color: #4d4d4d;
    font-weight: bold;
    width: 33.33%;
  }

  .pageTitle #time
  {
    font-size: 16px;
    color: #323232;
    margin-left: 20px;
  }
</style>



