<template>
  <div class="tem-content">
    <div class="leftDiv" >
      <div class="leftTop">
        <ul id="nav-List" >
          <li v-for="(it,index) in navList" @click="navClick(index)" :class="{active:navIndex==index}">
            {{it}}
          </li>
        </ul>
      </div>
      <div v-show="navIndex==0">
        <treeCom @selectItem="getSelect" :selectItem="selectItem" :model="treeList" ></treeCom>
      </div>
      <div v-show="navIndex==1" class="groupDiv">
        <div class="addGroup">
          +创建分组
        </div>
        <ul>
          <li v-for="(it,index) in groupList" @click="selectGroup(index)" :class="{groupActive:groupIndex==index}" >
            {{it.name}}
          </li>
        </ul>
      </div>
    </div>
    <div class="rightDiv">
      <div class="rightTop" >
        <span class="sixteenBlack thirty-" >手动</span><span class="sixteenBlack thirty-thirty" >|</span><span class="sixteenBlack">轮巡中-</span>
        <span class="sixteenBlack">巡视分组名称</span>
        <div class="floatRight twelveBlack screenGroup">
          <span v-for="(it,index) in screenList">
            <span class="septal" v-if="index!=0" >|</span>
            <span class="cursor" @click="selectScreen(index)" :class="{blueColor:index==screenIndex}">{{it.name}}</span>
          </span>
        </div>
      </div>
      <div class="rightContent" >
        2222111
      </div>
      <div class="rightCookie" >
        <div class="rightCookieTop">
          <ul id="navC-List" >
            <li v-for="(it,index) in navCentrolizeList" @click="navCentrolClick(index)" :class="{active:navCentrolIndex==index}">
              {{it}}
            </li>
          </ul>
        </div>
        <div class="centrolDiv" v-show="navCentrolIndex==0">
          1
        </div>
        <div class="centrolDiv" v-show="navCentrolIndex==1">
          2
        </div>
        <div class="centrolDiv" v-show="navCentrolIndex==2">
          3
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import treeCom from '../common/TreeCom'
export default {
  name: 'home',
  data () {
    return {
      msg: 'Welcome to 巡视！',
      navList: ['教室列表','轮巡分组'],
      navCentrolizeList:['信息','截图','录制'],
      navIndex:0,
      navCentrolIndex:0,
      selectItem: {},
      treeList: [{name:'1',children:[{name:'1.1'},{name:'1.2',children:[{name:'1.2.1'}]}]},
        {name:'2',children:[{name:'2.1'},{name:'2.2'}]},
        {name:'3',children:[{name:'3.1'}]}],
      groupList:[{id:1,name:'一号楼'},{id:2,name:'二号楼'},{id:3,name:'三号楼'}],
      groupIndex:0,
      screenList:[{name:'1分屏'},{name:'4分屏'},{name:'9分屏'},{name:'16分屏'}],
      screenIndex:0
    }
  },
  methods: {
    navClick(index){
      this.navIndex = index;
    },
    navCentrolClick(index){
      this.navCentrolIndex = index;
    },
    getSelect(e){
      this.selectItem = e;
    },
    selectGroup(index){
      this.groupIndex = index;
    },
    selectScreen(index){
      this.screenIndex = index;
    }
  },
  components: {treeCom}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .tem-content
  {
    height: 100%;
    background: #fff;
  }
  .leftDiv
  {
    width: 16.7%;
    height: 100%;
    float: left;
    background: #36393c;
  }

  .leftDiv>.leftTop
  {
    border-bottom: 2px solid #4f5c66;
    font-size: 18px;
    color: #e3e3e3;
  }

  .rightDiv
  {
    width: 83.3%;
    height: 100%;
    float: left;
  }

  .rightTop{
    height: 50px;
    line-height: 50px;
    background: #efefef;
  }

  .rightContent{
    width: 80%;
    height: calc(100% - 50px);
    overflow: auto;
    float: left;
  }
  .rightCookie>.rightCookieTop
  {
    border-bottom: 2px solid #4f5c66;
    font-size: 18px;
    color: #e3e3e3;
  }
  .rightCookie{
    width: 20%;
    height: calc(100% - 50px);
    float: right;
    background: #36393c;
  }
  ul,li{
    list-style: none;
    margin: 0px;
    overflow: hidden;
  }

  ul#nav-List{
    text-align: center;
  }

  ul#nav-List>li{
    max-width: 104px;
    width: 50%;
    text-align: center;
    padding: 36px 0px 5px 0px;
    margin-bottom: -2px;
    cursor: pointer;
    display: inline-block;
    border-bottom: 2px solid rgba(204,82,100,0);
  }

  ul#nav-List>li.active{
    color: #FFFFFF;
    border-bottom: 2px solid #2dabff;
  }

  ul#navC-List{
    text-align: center;
  }

  ul#navC-List>li{
    max-width: 68px;
    width: 33.33%;
    text-align: center;
    padding: 20px 0px 5px 0px;
    margin-bottom: -2px;
    cursor: pointer;
    display: inline-block;
    border-bottom: 2px solid rgba(204,82,100,0);
  }

  ul#navC-List>li.active{
    color: #FFFFFF;
    border-bottom: 2px solid #2dabff;
  }

  .groupDiv .addGroup{
    font-size: 16px;
    color: #2dabff;
    margin: 20px 0 10px 17%;
    cursor: pointer;
  }

  .groupDiv>ul>li{
    padding: 10px 20px 10px 17%;
    height: 30px;
    line-height: 30px;
    font-size: 16px;
    color: #b2b2b2;
    cursor: pointer;
  }

  .groupDiv>ul>li.groupActive{
    color: #FFFFFF;
    background: #2dabff;
  }
  .screenGroup{
    padding-right: 20px;
  }
  .centrolDiv{
    color: #b2b2b2;
  }
</style>



