<template>
  <div>
    <button @click="GoTo(1)" style="width: 54px;" >首页</button>
    <button @click="GoTo(pagination.pageIndex-1)" style="width: 54px;" >上页</button>
    <button v-for="it in totalPage" class="pageNum" :class="{pageCheck:pagination.pageIndex==it}" style="width: 31px;" @click="GoTo(it)" >{{it}}</button>
    <button @click="GoTo(pagination.pageIndex+1)" style="width: 54px;" >下页</button>
    <button @click="GoTo(totalPage)" style="width: 54px;" >尾页</button>
  </div>
</template>

<script>
  import packUp from '../../assets/icon/pack-up.png';
  import packUp2 from '../../assets/icon/pack-up2.png';
  import deploy1 from '../../assets/icon/deploy1.png';
  import deploy2 from '../../assets/icon/deploy2.png';

export default {
  name: 'pageCom',
  props: ['pagination'],
  data: function () {
    return {}
  },
  computed: {
    totalPage()
    {
        if (!this.pagination)
          throw new Error("PageCom:: 请设置正确的pagination值！");
        return Math.ceil(this.pagination.totalItems/this.pagination.pageSize);
    }
  },
  methods: {
    GoTo(page){
        if (page>0&&page<=this.totalPage)
        {
          this.pagination.pageIndex = page;
          this.$emit("goToPage",page);
        }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  button
  {
    height: 31px;
    border: none;
    cursor: pointer;
    background: #FFFFFF;
    color: #a6abc2;
    font-family: "Microsoft YaHei";
  }

  button:hover
  {
    background: #2dabff;
    color: #FFFFFF;
  }

  .pageCheck
  {
    background: #2dabff;
    color: #FFFFFF;
  }
  .pageNum
  {
    margin-left: 5px;
  }
</style>



