          <li v-for="(it,index) in navList" @click="navClick(index)" :class="{active:navIndex==index}">
            {{it}}
          </li>
        </ul>
        <div class="floatRight topRight gray-font">
          <div style="top: 13px;left: 5px;float: left;" class="squaredFour check_div">
            <input type="checkbox" id="topCheckAll" @click="checkAllFun" v-model="checkAll" />
            <label for="topCheckAll" @click ></label>
          </div>
          <span class="six-twenty cursor" for="topCheckAll" >全选</span>
          <span class="cursor" >全部设备</span>
          <img class="six-twenty cursor" src="../../assets/picture/set-deploy.png" />
          <span class="cursor">在线</span>
          <img class="cursor" style="margin-right: 40px;" src="../../assets/picture/set-deploy.png" />
          <img class="cursor switchButton" src="../../assets/picture/c-pic.png" @click="isSwitch=!isSwitch" />
        </div>
      </div>
      <div class="banner" v-show="navIndex==0">
        <button class="blackButton firstButton" @click="deviceOperator(0)" :disabled="onlineMacs.length==0" >上课</button>
        <button class="blackButton" :disabled="onlineMacs.length==0" @click="deviceOperator(1)" >下课</button>
        <button class="blackButton firstButton" style="width: 70px;" @click="deviceOperator(2)" >启用面板</button>
        <button class="blackButton" style="width: 70px;" @click="deviceOperator(3)" >禁用面板</button>
        <button class="blackButton firstButton" disabled="none" style="width: 60px;" @click="deviceOperator(4)" >开投影</button>
        <button class="blackButton" style="width: 60px;" disabled="disabled" @click="deviceOperator(5)" >关投影</button>
        <span class="firstButton" >幕布</span>
        <button class="buttonCls upButton" @click="deviceOperator(6)" ></button>
        <button class="buttonCls downButton" @click="deviceOperator(7)" ></button>
        <button class="blackButton firstButton" style="width: 70px;" @click="deviceOperator(8)" >云台复位</button>
        <button class="blackButton" @click="deviceOperator(9)" >重启</button>
        <button class="blackButton" style="width: 75px;" :disabled="!enterAble">
          进入导播间
        </button>
      </div>
      <div class="banner" v-show="navIndex==1">
        222
      </div>
      <div class="banner" v-show="navIndex==2">
        333
      </div>
      <div class="rightContent" >
        <ul id="devicelist" v-show="!isSwitch">
          <li @click="liClick(it.id)" v-for="(it,index) in deviceList" >
            <div class="li_top" v-if="index==1">
              <div class="check_pardiv">
                <div class="squaredFour check_div">
                  <input type="checkbox" :id="'check1_'+index" :value="it" v-model="checkList" />
                  <label :for="'check1_'+index" @click.prevent ></label>
                </div>
              </div>
              <div class="icon_div" style="margin-bottom: 10px;">
                <img src="../../assets/icon/c-projector.png" />
              </div>
              <div class="icon_div" style="margin-bottom: 10px;" >
                <img src="../../assets/icon/c-video.png" />
              </div>
              <div class="icon_div">
                <img src="../../assets/icon/c-lock.png" />
              </div>
              <div class="icon_div" style="position: relative;left: -4px;" >
                <img src="../../assets/icon/c-control.png" />
              </div>
            </div>
            <div class="li_top" style="background: #1fcd08;" v-if="index!=1">
              <div class="check_pardiv">
                <div class="squaredFour check_div">
                  <input type="checkbox" :id="'check1_'+index" :value="it" v-model="checkList" />
                  <label :for="'check1_'+index" @click.prevent ></label>
                </div>
              </div>
              <div class="icon_div" style="height: 76px;line-height: 76px;">
                <img src="../../assets/icon/c-play.png" />
              </div>
              <div class="icon_div" style="height: 76px;line-height: 76px;" >
                <img src="../../assets/icon/c-video.png" />
              </div>
            </div>
            <div class="li_bottom">
              <span style="color: #323232;">{{it.areaName}}</span> <br/>
              <span>{{it.techerName?it.techerName:''}}</span> <br/>
              <span>xx班级</span>
            </div>
          </li>
        </ul>
        <table v-show="isSwitch" >
          <tr class="td-head">
            <td>
              <div class="squaredFour check_div">
                <input type="checkbox" id="check_all2" @click="checkAllFun" v-model="checkAll" />
                <label for="check_all2"></label>
              </div>
            </td>
            <td><span>教室</span></td>
            <td><span>设备</span></td>
            <td><span>当前任务</span></td>
            <td><span>状态</span></td>
          </tr>
          <tr v-for="(it,index) in deviceList">
            <td>
              <div class="squaredFour check_div">
                <input type="checkbox" :id="'check2_'+index" :value="it" v-model="checkList" />
                <label :for="'check2_'+index" ></label>
              </div>
            </td>
            <td>{{it.areaName}}</td>
            <td>{{it.name}}</td>
            <td>{{it.techerName?(it.techerName+'老师正在上课'):'暂无任务'}}</td>
            <td>
              <img src="../../assets/icon/c-projector-s.png" />
              <img src="../../assets/icon/c-control-s.png" />
              <img src="../../assets/icon/c-lock-s.png" />
