        <span>录像机</span>
        <img src="../../assets/icon/c-play-s.png" />
        <span>直播课</span>
        <span style="color: #2dabff;">蓝色模块：中控设备</span>
        <span style="color: #1fcd08;">绿色模块：录播设备</span>
        <span>灰色模块：离线中</span>
        <page-com @goToPage="goToPage" class="pageDiv" :pagination="pagination" > </page-com>
