</template>

<script>
  import centralizedMServer from '../../service/centralizedMServer'
  import treeCom from '../common/TreeCom'
  import pageCom from '../common/PageCom'

  function isArray(v)
  {
    return Object.prototype.toString.apply(v) === '[object Array]';
  }
  Array.prototype.indexOfFun = function(val)
  {
    for (var i = 0; i < this.length; i++)
    {
      if (this[i] == val)
        return i;
    }
    return -1;
  };

  Array.prototype.remove = function(val)
  {
    var index = this.indexOfFun(val);
    if (index > -1)
    {
      this.splice(index, 1);
    }
  };

  Array.prototype.add = function(val)
  {
    var index = this.indexOfFun(val);
    if (index < 0)
    {
      this.push(val);
    }
  };

  export default {
    name: 'home',
    data () {
      return {
        msg: 'Welcome to 集控1！',
        treeList: [],
        selectItem: {},
        navList:['中控','录播','其他'],
        navIndex:0,
        checkList:[],
        deviceList:[],
        pagination: {
          totalItems: 21,
          pageSize: 10,
          pageIndex: 1
        },
        isSwitch:false,
        status:{classRoomObj:{},macs:"",timer:0,recordStatus:{LivingStart:'直播中',LivingStop:'未直播'},
          didTask:0}
      }
    },
    watch:{
        checkList:function (val) {
          console.log(val);
        }
    },
    mounted :function () {
      centralizedMServer.getTreeList().then(res=>{
        this.treeList = res;
        if (this.treeList&&this.treeList.length>0){
          this.selectItem = this.treeList[0];
          this.getDevices();
        }
      });
    },
    components : { treeCom,pageCom },
    methods: {
      getDevices(){
        centralizedMServer.getDeviceList({areainfo:this.selectItem.id}).then(res=>{
          let datas = [];
          this.status.classRoomObj = {};
          let macs = "";
          if (res.list&&res.list.length>0)
             res.list.forEach(it=>{
                 it.macs.forEach(sit=>{
                   this.status.classRoomObj[sit.MAC] = sit;
                   macs += sit.MAC + ',';
                   datas.push(sit);
                 });
             });
          this.deviceList = datas;
          if (macs.length>0)
            this.status.macs = macs.substring(0,macs.length-1);
          else
            this.status.macs = "";
          this.setClassRoomStatus(); // 第一次更新教室状态
          this.deviceRefresh(); // 定时查询教室状态
        });
      },
      setClassRoomStatus(){
        centralizedMServer.refresh({mac:this.status.macs}).then(data=>{
          if (data && isArray(data))
          {
            var macArr = [];
            this.refreshStatus(macArr,data);
          }
          else
            clearInterval(this.status.timer);
        });
      },
      refreshStatus(macArr,data){
        this.deviceList.forEach(it=>{
          this.initRoomStatus(it);
        });
        data.forEach(function(it){
          let key = '';
          let val = '';
          for (let i in it)
          {
            key = i;
            val = it[key];
          }
          if (this.status.classRoomObj[key])
          {
            macArr.push(key);
            let classRoom = this.status.classRoomObj[key];
            classRoom.starting = true;
            let statusObj = Object.create(null);
            let keyArr = val.split(',,');
            let i=0;
            let j=0;
            classRoom.recordstatus = '未直播';
            while(i<keyArr.length)
            {
              if ($.trim(keyArr[i])=='true' || $.trim(keyArr[i])=='false')
              {
                statusObj['card'+j] = ($.trim(keyArr[i])=='false'?true:false);
                j++;
              }
              if ($.trim(keyArr[i])=='LivingStart' || $.trim(keyArr[i])=='LivingStop')
                classRoom.recordstatus = this.status.recordStatus[$.trim(keyArr[i])];
              i++;
            }
            classRoom.statusObj = statusObj;
          }
        });
      },
      initRoomStatus(classRoom){
        classRoom.starting = false;
        if (classRoom.statusObj)
        {
          for (var i in classRoom.statusObj)
            classRoom.statusObj[i] = false;
        }
      },
      deviceRefresh(){
        this.status.timer = setInterval(this.setClassRoomStatus,10000); // 刷新教师设备状态
      },
      getSelect(e){
        clearInterval(this.status.timer);
        this.status.macs = ""
        this.status.classRoomObj = {};
        this.selectItem = e;
        this.getDevices();
      },
      navClick(index){
          this.navIndex = index;
      },
      goToPage(index){
          console.log(index);
      },
      liClick(id){
          if (this.checkList.indexOfFun(id)>-1)
              this.checkList.remove(id);
          else
              this.checkList.add(id);
      },
      checkAllFun(){
          if (!this.checkAll)
          {
            var checkList = this.checkList;
            this.deviceList.forEach(function(it){
              checkList.add(it);
            });
          }
          else
            this.checkList = [];
      },
      deviceOperator(num){
          switch(num){
            // 上课
            case 0:
                centralizedMServer.sendCentralControlCmd("CsnGroupCmd:OpenCSN",this.onlineMacs);
                break;
            // 下课
            case 1:
              centralizedMServer.sendCentralControlCmd("CsnGroupCmd:CloseCSN",this.onlineMacs);
                break;
            // 启用面板
            case 2:
              centralizedMServer.sendCentralControlCmd("CsnGroupCmd:EnablePanel",this.onlineMacs);
              break;
            // 禁用面板
            case 3:
              centralizedMServer.sendCentralControlCmd("CsnGroupCmd:DisablePanel",this.onlineMacs);
              break;
            // 开投影
            case 4:

              break;
            // 关投影
            case 5:

              break;
            // 打开幕布
            case 6:
              break;
            // 关闭幕布
            case 7:
              break;
            // 云台复位
            case 8:
              var arr = this.onlineMacs.split(',');
              this.status.tasks = [];
              this.status.didTask = 0;
              var vue = this;
              arr.forEach(it=>{
                this.status.tasks.push(
                    (function(mac,macs){
                       return function(){
                         centralizedMServer.sendCmdToPtzs(mac).then(res=>{
                           vue.status.didTask++;
                             if (vue.status.didTask==arr.length)
                               centralizedMServer.sendCmdToPtz(macs).then(res=>{

                               })
                         })
                       }
                    })(it,this.onlineMacs));
              });
              this.status.tasks.forEach(it=>{
                  it();
              })
              break;
            // 重启
            case 9:
              centralizedMServer.sendCentralControlCmd("RecordCmd=Reboot",this.onlineMacs);
              break;
          }
      }
    },
    computed:{
        // 是否可以进入导播间
      enterAble(){
        return this.checkList.length==1;
        },
      checkAll:{
          get(){
            if (this.checkList.length==0)
              return false;
            else
              return this.checkList.length==this.deviceList.length;
          },
          set(newValue){
            console.log('------>set',newValue);
          }
      },
      onlineMacs(){
        var macs = "";
        this.checkList.forEach(it=>{
          //if (it.starting)
              macs += it.MAC + ',';
            });
        if (macs.length>0)
            macs = macs.substring(0,macs.length-1);
        return macs;
      }
    }
  }
</script>

