import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Home from '@/components/home/Home'
import CentralizedManager from '@/components/centralizedmanager/CentralizedManager'
import Patrol from '@/components/patrol/Patrol'
import PatrolExam from '@/components/patrolexam/PatrolExam'
import Video from '@/components/video/Video'
import Subscribe from '@/components/subscribe/Subscribe'
import Set from '@/components/set/Set'
import Cards from '@/components/set/card/Cards'
import Classes from '@/components/set/class/Classes'
import ClassRooms from '@/components/set/classRoom/ClassRooms'
import Devices from '@/components/set/device/Devices'
import DeviceConfig from '@/components/set/deviceConfig/DeviceConfig'
import Managers from '@/components/set/manager/Managers'
import Organizations from '@/components/set/organization/Organizations'
import PatrolPlan from '@/components/set/patrolPlan/PatrolPlan'
import SuperviseModel from '@/components/set/superviseModel/SuperviseModel'
import Users from '@/components/set/user/Users'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      children:[
        {
          path: 'Home',
          component: Home
        },
        {
          path: 'CentralizedManager',
          component: CentralizedManager
        },
        {
          path: 'Patrol',
          component: Patrol
        },
        {
          path: 'PatrolExam',
          component: PatrolExam
        },
        {
          path: 'Video',
          component: Video
        },
        {
          path: 'Subscribe',
          component: Subscribe
        },
        {
          path: 'Set',
          component: Set,
          children:[
            {
              path: 'Devices',
              component: Devices
            },
            {
              path: 'ClassRooms',
              component: ClassRooms
            },
            {
              path: 'Cards',
              component: Cards
            },
            {
              path: 'Classes',
              component: Classes
            },
            {
              path: 'DeviceConfig',
              component: DeviceConfig
            },
            {
              path: 'Managers',
              component: Managers
            },
            {
              path: 'Organizations',
              component: Organizations
            },
            {
              path: 'PatrolPlan',
              component: PatrolPlan
            },
            {
              path: 'SuperviseModel',
              component: SuperviseModel
            },
            {
              path: 'Users',
              component: Users
            }
          ]
        }
      ]
    }
  ]
})



